.card {
    border: 1px solid rgba(160, 160, 160, 0.603);
    border-radius: 10px;
}

.card:hover{
    transform: translateY(-3px);
    transition: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    box-shadow: 2px 2px #dfdfdf;
}

.card:hover .avatar {
    background-color: bisque;
    transition: cubic-bezier(0.755, 0.05, 0.855, 0.06);

}


.card_content {

    padding:1em;
    display: grid;
    grid-template-rows: 75px 1fr;
    grid-template-columns: 250px;
}
.card_container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 1em;
}

.card_container > * {
    margin: 5px;
}

.avatar {
    height: 50px;
    width: 50px;
    background-color: lightgray;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: gray;
    font-weight: 700;
    font-size: x-large;
    
}

.searchBar {
    padding: 0.5rem;
    margin: 2em;
    border: var(--adoblue) solid thin ;
    border-radius: 5px;
    text-align: center;
}




